@font-face {
  font-family: "Montserrat";
  src: url("./assets/fonts/montserrat-regular-webfont.woff2") format("woff2"),
    url("./assets/fonts/montserrat-regular-webfont.woff") format("woff"),
    url("./assets/fonts/Montserrat-Regular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-weight: 700;
  font-family: "Montserrat";
  font-style: normal;
  src: url("./assets/fonts/montserrat-bold-webfont.woff2") format("woff2"),
    url("./assets/fonts/montserrat-bold-webfont.woff") format("woff"),
    url("./assets/fonts/Montserrat-Bold.ttf") format("ttf");
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
}
.app {
  background: center / cover #0f1e35 url("./assets/images/background.jpg") no-repeat;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.add-block_controls {
  display: flex;
  justify-content: space-between;
}
.add-block_submit {
  background: #39C071;
  border-radius: 3px;
  font-weight: bold;
  font-family: 'Montserrat';
  font-size: 13px;
  line-height: 19px;
  border: none;
  color: #fff;
  padding: 7px 8px 4px 8px;
}
.add-block_cancel {
  border: none;
  background: transparent;
  padding: 0;
}